<template>
  <div>
    <div id="slider"
         class="mui-slider">
      <div id="sliderSegmentedControl"
           class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
        <div class="mui-scroll">
          <a :class="['mui-control-item',item.id == 0 ? 'mui-active' : '']"
             v-for="item in cates"
             :key="item.id"
             @click="getPhotoListByCateId(item.id)">
            {{item.title}}
          </a>
        </div>
      </div>
      <ul class="photo-list">
        <router-link v-for="item in imgList"
                     :key="item.id"
                     :to="'/home/photoinfo/'+item.id"
                     tag="li">
          <img v-lazy="item.img_url">
          <div class="info">
            <h1 class="info-title">{{item.title}}</h1>
            <div class="info-body">{{item.zhaiyao}}</div>
          </div>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>

export default {
  name: 'PhotoList',
  data () {
    return {
      cates: [],
      imgList: []
    }
  },
  methods: {
    getAllCategory () {
      this.$ajax.get('api/getimgcategory').then(res => {
        if (res.data.status === 0) {
          res.data.message.unshift({ title: '全部', id: 0 })
          this.cates = res.data.message
        }
      })
    },
    getPhotoListByCateId (cateId) {
      this.$ajax.get('api/getimages/' + cateId)
        .then(res => {
          if (res.data.status === 0) {
            this.imgList = res.data.message
          }
        })
    }
  },
  mounted () {
    this.mui('.mui-scroll-wrapper').scroll({
      deceleration: 0.0005 // flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
    })
  },
  created () {
    this.getAllCategory()
    this.getPhotoListByCateId(0)
  }
}
</script>

<style lang="stylus" scoped>
*
  touch-action: pan-y
.photo-list
  list-style: none
  padding: 10px
  padding-bottom: 0
  li
    background-color: #ccc
    text-align: center
    margin-bottom: 10px
    box-shadow: 0 0 9px #999
    position: relative
    img
      width: 100%
      vertical-align: middle
    img[lazy=loading]
      width: 40px
      height: 300px
      margin: auto
    .info
      color: #fff
      text-align: left
      position: absolute
      bottom: 0
      background-color: rgba(0, 0, 0, 0.4)
      max-height: 100px
      overflow: hidden
      padding: 0 5px
      .info-title
        font-size: 14px
      .info-body
        font-size: 13px
</style>



