<template>
  <div id="app">
    <mt-header fixed
               title="仿黑马程序员VUE项目">
      <span slot="left"
            @click="goBack"
            v-show="flag">
        <mt-button icon="back">返回</mt-button>
      </span>
    </mt-header>
    <transition>
      <router-view />
    </transition>
    <nav class="mui-bar mui-bar-tab">
      <router-link class="mui-tab-item-myy"
                   to="/home">
        <span class="mui-icon mui-icon-home"></span>
        <span class="mui-tab-label">首页</span>
      </router-link>
      <router-link class="mui-tab-item-myy"
                   to="/member">
        <span class="mui-icon mui-icon-contact"></span>
        <span class="mui-tab-label">会员</span>
      </router-link>
      <router-link class="mui-tab-item-myy"
                   to="/shopcar">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cart"><span class="mui-badge"
                id="badge">{{$store.getters.getAllCount}}</span></span>
        <span class="mui-tab-label">购物车</span>
      </router-link>
      <router-link class="mui-tab-item-myy"
                   to="/search">
        <span class="mui-icon mui-icon-search"></span>
        <span class="mui-tab-label">搜索</span>
      </router-link>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      flag: false
    }
  },
  methods: {
    goBack () {
      this.$router.go(-1)
    }
  },
  watch: {
    '$route.path': function (newVal) {
      if (newVal === '/home') {
        this.flag = false
      } else {
        this.flag = true
      }
    }
  },
  created () {
    if (this.$route.path === '/home') {
      this.flag = false
    } else {
      this.flag = true
    }
  }
}
</script>

<style lang="stylus">
*
  padding: 0
  margin: 0
body
  background-color: #fff
#app
  padding-top: 40px
  padding-bottom: 50px
  overflow-x: hidden
#app .mint-header
  z-index: 9999
.v-enter
  opacity: 0
  transform: translateX(100%)
  position: absolute
.v-leave-to
  opacity: 0
  transform: translateX(-100%)
  position: absolute
.v-enter-active, .v-leave-active
  transition: all 0.5s ease
.mui-bar-tab .mui-tab-item-myy.mui-active
  color: #007aff
.mui-bar-tab .mui-tab-item-myy
  display: table-cell
  overflow: hidden
  width: 1%
  height: 50px
  text-align: center
  vertical-align: middle
  white-space: nowrap
  text-overflow: ellipsis
  color: #929292
.mui-bar-tab .mui-tab-item-myy .mui-icon
  top: 3px
  width: 24px
  height: 24px
  padding-top: 0
  padding-bottom: 0
.mui-bar-tab .mui-tab-item-myy .mui-icon ~ .mui-tab-label
  font-size: 11px
  display: block
  overflow: hidden
  text-overflow: ellipsis
</style>



